@media(max-width: 991px) {
	.footer__main-box {
		flex-direction: column;
	}
	.footer__wrapp {
		position: relative;
		padding-bottom: 12px;
		max-width: 100%;
		overflow: hidden;
	}
	.footer__form {
		padding-top: 30px;
	}
	.footer__bottom-wrapp {
		flex-direction: column;
	}
	.footer__content-title {
		max-width: 282px;
	}
	.content__list {
		padding-top: 0;
		height: 0;
		overflow-y: auto;
	}
	.content__item {
		padding-bottom: 6px;
	}
	.content__item:last-child {
		padding-bottom: 0;
	}
	.content__title {
		padding: 8px 0;
		position: relative;
		display: flex;
		align-items: center;
		cursor: pointer;
		&::after {
			content: '';
			position: absolute;
			top: 40%;
			right: 10px;
			width: 6px;
			height: 6px;
			background-color: transparent;
			border: 2px solid #fff;
			border-left: none;
			border-top: none;
			transform: translate(0, -60%);
			transform: rotate(-45deg);
		}
	}
	.content__list-active {
		padding-bottom: 10px;
		height: 100%;
	}
	.content__title-active.content__title::after {
		transform: rotate(45deg);
	}
	.footer__bottom-copyright.none {
		display: none;
	}
	.footer__bottom-copyright.block {
		display: block;
		text-align: center;
	}
	.footer__bottom {
		padding-top: 20px;
		display: flex;
    	justify-content: center;
	}
	.footer__bottom-social {
		padding-bottom: 16px;
	}
	.footer__bottom-pay {
		padding-bottom: 24px;
	}
	.footer__bottom-wrapp {
		align-items: center;
	}
	.pay__title h3 {
		text-align: center;
	}
	.social__title h3 {
		text-align: center;
	}
	.social__icon:first-child {
		padding-left: 7px;
	}
	.pay__wrapp {
		height: auto;
	}
}
